import controller from './cardItemList.controller'

export default function cardItemList(){
  "ngInject";
  return {
    restrict: 'E',
    scope: {
      collection:"=",
      collectionResults:"=",
      searchFilter:"=",
      hasNew:"=?",
      tooltip:"@",
      onClickNew:"&?",
      searchLabel:"@"
    },
    template: require("./cardItemList.tpl.html"),
    controller: controller.UID,
    controllerAs: "vm",
    bindToController: true,
    transclude:true,
    require:['cardItemList', '?^^menuItemList', '?^^menuSectionList', '?^^modifierList', '?^^outletLocationList', '?^^outletList'],
    link: (scope, el, attr, ctrl) => {
      if (ctrl[1]){
        ctrl[1].cardItemList = ctrl[0];
      } else if (ctrl[2]){
        ctrl[2].cardItemList = ctrl[0];
      } else if (ctrl[3]){
        ctrl[3].cardItemList = ctrl[0];
      } else if (ctrl[4]){
        ctrl[4].cardItemList = ctrl[0];
      } else if (ctrl[5]){
        ctrl[5].cardItemList = ctrl[0];
      }
    }
  }
}



